@import url(https://fonts.googleapis.com/css2?family=Ranchers&display=swap);
.name-cell{
    text-transform: capitalize;
    background-color: #6495ED;
    color: black;
    font-weight: bold;
}
.datatable .col {
    cursor: pointer;
    background-color: #6495ED;
    color: white;
}

.datatable .col .pointer:after {
    font-size: 80%;
    content: "";
    margin-right: 18px;
}

.datatable .col .pointer:hover:after {
    font-size: 80%;
    content: "ðŸ”»";
    margin-right: 0;
}
.searchbox button{
    border: 1px solid;
    background-color: #6495ED;
    border-color: #6495ED;
    color: white;
    font-weight: 800;
}
.searchbox button:hover {
    background-color:white;
    color: black;
}
.navbar {
    padding: 20px 25px;
    margin: 0 auto;

}

.navbar-collapse {
    margin: 0 auto;
    justify-content: center;
    align-items: flex-start;
}

.search-area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.data-area {
    margin: 0 auto;
    width: 90%;
}


.header {
    padding: 30px 0;
    font-weight: 300;
    color: #ffffff;
    background-color: #6495ED;
    width: 100%;
    text-align: center;
    border-bottom: 10px solid lightblue;
    margin-bottom: 10px;
    font-family: 'Ranchers';
}
* {
  padding:0;
  margin:0;
}

.App {
  text-align: center;
  font-family: sans-serif;
}

.App-logo {
  animation: App-logo-spin 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #7194d8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61fb6e;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

